
.switches {
  display: flex;
  align-items: center;
  width: 240px;
  margin: 0 auto;
  border: 1px solid #333;
  border-radius: 5px;
}
.switches .switch-item {
  flex: 1;
  padding: 8px;
  text-align: center;
  font-size: 14px;
  color: rgba(255,255,255,0.3);
}
.switches .switch-item.active {
  background: #333;
  color: #fff;
}


