
.song-list .item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  height: 64px;
  font-size: 14px;
}
.song-list .item .rankicon {
  flex: 0 0 25px;
  width: 25px;
  margin-right: 30px;
  text-align: center;
}
.song-list .item .rankicon .icon {
  display: inline-block;
  width: 25px;
  height: 24px;
  background-size: 25px 24px;
}
.song-list .item .rankicon .icon.icon0 {
  background-image: url("first@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.song-list .item .rankicon .icon.icon0 {
    background-image: url("first@3x.png");
}
}
.song-list .item .rankicon .icon.icon1 {
  background-image: url("second@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.song-list .item .rankicon .icon.icon1 {
    background-image: url("second@3x.png");
}
}
.song-list .item .rankicon .icon.icon2 {
  background-image: url("third@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.song-list .item .rankicon .icon.icon2 {
    background-image: url("third@3x.png");
}
}
.song-list .item .rankicon .text {
  color: #ffcd32;
  font-size: 18px;
}
.song-list .item .content {
  flex: 1;
  line-height: 20px;
  overflow: hidden;
}
.song-list .item .content .name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #fff;
}
.song-list .item .content .desc {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-top: 4px;
  color: rgba(255,255,255,0.3);
}


